.fullscreen-text {
  position: relative;
  z-index: 3;
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
  padding: 1.5rem;
  text-align: center; }

.fullscreen-text--blue {
  background: rgba(0, 148, 216, 0.5);
  color: white;
  transition: background 500ms ease-in-out; }
  @media (min-width: 800px) {
    .fullscreen-text--blue {
      background: rgba(0, 148, 216, 0.25); } }

.fullscreen-image {
  position: relative;
  z-index: 1;
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover; }

.fullscreen-video {
  display: none; }
  @media (min-width: 800px) {
    .fullscreen-video {
      position: absolute;
      z-index: 2;
      display: block;
      top: 50%;
      left: 50%;
      min-width: 100%;
      min-height: 100%;
      width: auto;
      height: auto;
      z-index: -100;
      transform: translateX(-50%) translateY(-50%);
      background-repeat: no-repeat;
      background-size: cover;
      transition: 1s opacity; } }

.greeting {
  font-family: "Georgia", serif;
  font-style: italic; }
  .greeting a {
    display: inline-block;
    padding: 0 0.125em;
    background: rgba(0, 148, 216, 0.25);
    text-decoration: none;
    transition: background 500ms ease-in-out; }
    .greeting a:hover, .greeting a:focus, .greeting a:active {
      background: #0094d8; }

.greeting-title {
  margin: 0;
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-size: 12vw;
  text-transform: uppercase; }
  @media (min-width: 800px) {
    .greeting-title {
      font-size: 100px; } }

.greeting-desc {
  max-width: 15em;
  margin: 0 auto;
  font-size: 5vw; }
  @media (min-width: 800px) {
    .greeting-desc {
      font-size: 42px; } }

.article-header,
.article-body,
.article-info {
  max-width: 47rem;
  margin: 1.5rem   auto; }

.article-image {
  width: 100%;
  height: 0;
  padding-bottom: 75%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center; }
  @media (min-width: 500px) {
    .article-image {
      padding-bottom: 40%; } }

.article-info {
  padding: 0.5rem 1rem;
  border: 1px solid #eae4de;
  background: #f9f6f3; }
  .article-info h3 {
    color: #0094d8; }

.title {
  text-align: center;
  text-transform: uppercase; }

.tagline {
  opacity: 0.5;
  text-align: center;
  font-size: 1.125rem;
  font-weight: normal; }

.preamble {
  text-align: center;
  font-size: 1.125rem; }

/*
  Base
*/
html {
  box-sizing: border-box;
  line-height: 1.7; }
  html *,
  html *::before,
  html *::after {
    box-sizing: inherit; }

img {
  max-width: 100%; }

a {
  color: #0094d8; }
